<template>
  <div style="background-color:#F9F9F9;height:100%;overflow:scroll;" ref="scrollView">
    <div style="position:relative;" @click="gotoProfileSetting">
      <van-image style="width: 100%;" fit="cover" :src="topImage" require />
      <div class="topBgView">
        <van-image
          round
          width="70"
          height="70"
          fit="cover"
          :src="(userIcon == '' || userIcon ==undefined )?require('@/assets/images/default/user_default_avata.png') : userIcon"
        />
        <div class="textContent">
          <span class="userName">{{nickName}}</span>
          <span class="clickTips">点击进入个人设置</span>
        </div>
      </div>
    </div>

    <div style="position: relative;top:-50px;">
      <van-row class="user-links">
        <van-col
          span="6"
          v-for="(link,index)  in userLinks"
          :key="link.name"
          @click="topTabClicked(index)"
        >
          <div class="item">
            <img style="width: 65px;height: 65px;font-size: 17px;" :src="link.src" />
            <span style="padding-top: 5px;color:#333333;">{{link.name}}</span>
          </div>
        </van-col>
      </van-row>

      <van-cell
        class="user-group"
        title="我的订单"
        value="查看全部订单"
        value-class="seeAllOrder"
        is-link
        :border="false"
        @click="pushToAllOderList"
      />
      <van-divider class="seperateLine" />

      <div style="position: relative; margin-bottom: 10px;">
        <van-row class="order-links" type="flex" justify="space-around">
          <van-col
            class="item"
            span="4"
            v-for="(link,index)  in orderLinks"
            :key="link.name"
            @click="pushToOrderList(index)"
          >
            <img  :src="link.src" />
            <span style="padding: 2px;color:#333333;">{{link.name}}</span>
            <div
              class="badgeNum"
              :class="{badgeNumMax:Number(orderNums[index]) > 99}"
              v-if="orderNums[index] != '0'"
            >{{Number(orderNums[index]) > 99 ? '99+' : orderNums[index]}}</div>
          </van-col>
        </van-row>
      </div>

      <van-cell-group>
        <van-cell class="mineCellStyle" title="我的购物车" @click="gotoCart">
          <img
            slot="right-icon"
            class="rightIcon"
            src="@/assets/images/personal_center/mine_car.png"
          />
        </van-cell>
        <van-cell class="mineCellStyle" title="我的收藏" @click="gotoMyCollect">
          <img
            slot="right-icon"
            class="rightIcon"
            src="@/assets/images/personal_center/mine_collection.png"
          />
        </van-cell>
        <van-cell class="mineCellStyle" title="我的收货地址" @click="gotoAddressList">
          <img
            slot="right-icon"
            class="rightIcon"
            src="@/assets/images/personal_center/mine_address.png"
          />
        </van-cell>
        <van-cell class="mineCellStyle" title="中奖记录" @click="gotoPrizeRecord">
          <img
            slot="right-icon"
            class="rightIcon"
            src="@/assets/images/personal_center/mine_recode.png"
          />
        </van-cell>
        <van-cell class="mineCellStyle" title="账号设置" @click="gotoSetting">
          <img
            slot="right-icon"
            class="rightIcon"
            src="@/assets/images/personal_center/mine_setting.png"
          />
        </van-cell>
        <van-cell class="mineCellStyle" title="关于我们" @click="gotoAboutUs" >
          <img
            slot="right-icon"
            class="rightIcon"
            src="@/assets/images/personal_center/mine_aboutus.png"
          />
        </van-cell>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
import { getLatestOrderNum } from "@/services/MineService";
import { Dialog } from "vant";

import { getUserInfo } from "@/services/MineService";
export default {
  name: "MineHomeThemeL",
  data() {
    return {
      offSetY: 0,
      userIcon: "",
      nickName: "",
      orderNumObj: {},
      topImage: require("@/assets/images/personal_center/user_topbg_img.png"),
      userLinks: [
        {
          name: "房屋管理",
          src: require("@/assets/images/personal_center/user_mainIcon_houses.png"),
          action: "manageHouses"
        },
        {
          name: "家庭成员",
          src: require("@/assets/images/personal_center/user_mainIcon_members.png"),
          action: "memberList"
        },
        {
          name: "优惠券",
          src: require("@/assets/images/personal_center/user_mainIcon_coupons.png"),
          action: "couponList"
        },
        {
          name: "积分",
          src: require("@/assets/images/personal_center/user_mainIcon_points.png"),
          action: "points"
        }
      ],
      orderLinks: [
        {
          name: "待付款",
          src: require("@/assets/images/personal_center/user_order_icon_waiPayment.png")
        },
        {
          name: "待收货",
          src: require("@/assets/images/personal_center/user_order_icon_waiGoods.png")
        },
        {
          name: "待评价",
          src: require("@/assets/images/personal_center/user_order_icon_waiComments.png")
        },
        {
          name: "已完成",
          src: require("@/assets/images/personal_center/user_order_icon_waiSending.png")
        },
        {
          name: "售后",
          src: require("@/assets/images/personal_center/user_order_icon_refund.png")
        }
      ]
    };
  },
  methods: {
    // 更新用户信息
    updateUserInfo: function() {
      let param = { OWNER_CODE: this.$userInfo.OWNER_CODE };
      getUserInfo(param, false)
        .then(data => {

          let info = this.$userInfo;
          info.PIC_URL = data.PIC_URL ? data.PIC_URL : "";
          info.OWNER_NICK_NAME = data.OWNER_NICK_NAME;
       
          this.userIcon = info.PIC_URL;
          this.nickName = info.OWNER_NICK_NAME;
          this.$setUserInfo(info);
        })
        .catch(error => {
          // Toast(error);
          console.log(error);
        });
    },
    gotoAddressList() {
      this.$router.push({ name: "shippingAddressList", query: { flag: 1 } });
    },
    gotoCart() {
      this.$router.push("/shoppingCart");
    },
    gotoProfileSetting() {
      this.$router.push("/userProfile");
    },
    gotoPrizeRecord() {
      this.$router.push("/prizeRecord");
    },
    gotoDoorManager(){
      this.$router.push("/phoneDoorManager");
    },
    gotoAboutUs() {
      this.$router.push("/aboutUs");
    },
    gotoMyCollect() {
      this.$router.push("/myCollect");
    },
    gotoSetting() {
      this.$router.push("/userSetting");
    },
    topTabClicked(index) {
      console.log(index);
      if (index != 3 && index != 0 && this.$userInfo.isNoArea == 1) {
        Dialog.confirm({
          title: "是否前往绑定房间？",
          confirmButtonText: "前往"
        })
          .then(() => {
            //跳转到绑定房产页面
            this.$router.push("selectArea");
          })
          .catch(() => {});
        return;
      }
      if (index == 0) {
        this.$router.push("/myHouses");
        return;
      }
      if (index == 1) {
        this.$router.push("/myFamily");
        return;
      }
      if (index == 2) {
        this.$router.push("/couponList");
        return;
      }
      if (index == 3) {
        this.$router.push("/myPoints");
        return;
      }
    },
    pushToAllOderList() {
      this.$router.push({ name: 'myOrderList', params: { orderType: 0 } });
    },
    pushToOrderList(index) {
      if (index != 4) {
        this.$router.push({
          name: 'myOrderList',
          params: { orderType: index + 1 }
        });
        return;
      }
      this.$router.push("/afterSaleList");
    },
    initLatestOrderNum() {
      let param = {
        ownerCode: this.$userInfo.OWNER_CODE,
        ownerPhone: this.$userInfo.PHONE
      };
      console.log(param);
      getLatestOrderNum(param)
        .then(data => {
          this.orderNumObj = data;
        })
        .catch(error => {
          //this.$toast(error);
          console.log("【Error】initLatestOrderNum：" + error);
        });
    },
    watchScroll() {
      window.addEventListener(
        "scroll",
        () => {
          let scrollTop = this.$refs.scrollView.scrollTop;
          //console.log(scrollTop);
          this.offSetY = scrollTop;
        },
        true
      );
    }
  },
  mounted() {
    console.log(this);
    console.log(this.$userInfo);
    this.watchScroll();
  },
  activated() {
    console.log('666666')
    this.initLatestOrderNum();
    this.$refs.scrollView.scrollTop = this.offSetY;
    //更新用户头像
    this.updateUserInfo();
  },
  computed: {
    orderNums() {
      if (Object.keys(this.orderNumObj).length == 0) {
        return ["0", "0", "0", "0", "0"];
      }
      let obj = this.orderNumObj;
      let arr = [];
      arr.push(obj.obligationCnt);
      arr.push(obj.shippedCnt);
      arr.push(obj.unevaluatedCnt);
      arr.push("0");
      arr.push(obj.goodsReturnCnt);
      console.log(arr);
      return arr;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.topBgView {
  position: absolute;
  left: 20px;
  top: 50%;
  margin-top: -60px;
  display: flex;
}

.textContent {
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 10px;
}

.userName {
  font-family: PingFangSC-Semibold;
  font-size: 26px;
  color: #ffffff;
  letter-spacing: -0.08px;
  max-width:280px;
}

.clickTips {
  font-family: PingFangSC-Regular;
  font-size: 15px;
  color: #ffffff;
  letter-spacing: -0.04px;
  margin-top: 3px;
}

.user-poster {
  width: 100%;
  height: 53vw;
  display: block;
}

.user-group {
  color: #333333;
  margin-top: 10px;
}

.user-links {
  padding: 15px 0;
  font-size: 12px;
  text-align: center;
  background-color: #fff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.order-links {
  padding: 15px 0;
  font-size: 12px;
  text-align: center;
  background-color: #fff;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 17px;
  img{
    width: 30px;
    height: 30px;
  }
}

.seperateLine {
  margin: 0px;
  color: "#1989fa";
  padding: 0 16px;
  height: 1px;
}

.badgeNum {
  position: absolute;
  box-sizing: border-box;
  //padding: 0;
  color: #fff;
  font-weight: 500;
  font-size: 11px;
  font-family: PingFang SC, Helvetica Neue, Arial, sans-serif;
  text-align: center;
  //border: 1px solid #fff;
  -webkit-transform: translate(70%, -50%);
  transform: translate(70%, -50%);
  -webkit-transform-origin: 100%;
  transform-origin: 100%;

  background-color: #FC0238;
  width: max-content;
  min-width: 19px;
  height: 19px;
  line-height: 17px;
  border-radius: 9.5px;
  top:20px;

  display:flex;
  justify-content:center;
  align-items:center;
}
/*for 99+*/
.badgeNumMax {
  padding: 0 3px;
  min-width: 30px;
}

.messageNum {
  position: absolute;
  box-sizing: border-box;
  top: -7px;
  right: 0;
  min-width: 16px;
  padding: 0 3px;
  color: #fff;
  font-weight: 500;
  font-size: 12px;
  font-family: PingFang SC, Helvetica Neue, Arial, sans-serif;
  line-height: 14px;
  text-align: center;
  background-color: #ee0a24;
  border: 1px solid #fff;
  border-radius: 16px;
}

.rightIcon {
  height: 30px;
  width: 30px;
}
/deep/[class*="van-hairline"]::after {
  position: absolute;
  box-sizing: border-box;

  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
}
.mineCellStyle {
  font-size: 20px;
  height: 58px;
  font-family: PingFangSC-Regular;
  vertical-align: center;
  align-items: center;
  color:#333333;
}
.van-cell:not(:last-child):after {
  border-bottom: 2px solid #ebedf0;
  right: 0.42667rem;
}
/deep/.user-group .van-cell__title {
  font-size: 20px;
}
/deep/.user-group .van-cell__value {
  font-size: 17px;
}
.seeAllOrder{
  color: #333333;
}
</style>
